import getArtboard from './artboard';
import getAssets from './assets';
const getStore = (page, selectedArtboard, sketch, callback) => {
    // get final store items
    const artboard = getArtboard(selectedArtboard, sketch);
    const assets = getAssets(page, artboard, sketch);
    const notes = [];
    // remove duplicate artboard
    artboard.remove();
    // return callback
    callback(Object.assign(Object.assign({}, assets), { artboard,
        notes }));
};
export default getStore;
