const createSvgFromLayer = (page, layer, sketch, id) => {
    let borderSize = 0;
    const activeBorders = layer.style.borders.filter((border) => border.enabled);
    if (activeBorders) {
        activeBorders.forEach((border) => {
            if (border.thickness > borderSize) {
                borderSize = border.thickness;
            }
        });
    }
    // duplicate layer
    const layerDuplicate = layer.duplicate();
    // set parent to page
    layerDuplicate.parent = page;
    // opacity and transforms will be applied on the div, not svg
    layerDuplicate.style.opacity = 1;
    layerDuplicate.transform.rotation = 0;
    layerDuplicate.transform.flippedHorizontally = false;
    layerDuplicate.transform.flippedVertically = false;
    // export duplicate layer
    sketch.export(layerDuplicate, {
        formats: 'svg',
        // @ts-ignore
        output: NSTemporaryDirectory(),
        ['use-id-for-name']: true,
        overwriting: true
    });
    // update layer frame to include bordersize
    layer.frame.width = Math.round(layer.frame.width + (borderSize * 1.5));
    layer.frame.height = Math.round(layer.frame.height + (borderSize * 1.5));
    layer.frame.x = Math.round(layer.frame.x - ((borderSize * 1.5) / 2));
    layer.frame.y = Math.round(layer.frame.y - ((borderSize * 1.5) / 2));
    // remove duplicate layer
    layerDuplicate.remove();
    // return AppAsset
    return {
        id: id ? id : layer.id,
        // @ts-ignore
        src: `${NSTemporaryDirectory()}${layerDuplicate.id}.svg`
    };
};
const createShapeFromLayer = (layer, sketch, name) => {
    // create new shape
    const shapeReplacement = new sketch.Shape({
        name: name ? name : layer.name,
        frame: layer.frame,
        style: layer.style,
        transform: {
            rotation: layer.transform.rotation,
            flippedHorizontally: layer.transform.flippedHorizontally,
            flippedVertically: layer.transform.flippedVertically
        }
    });
    // return new shape
    return shapeReplacement;
};
const createShapeFromGroup = (layer, sketch, prefix) => {
    // remove prefix from name
    const newName = layer.name.substr(prefix.length, layer.name.length - prefix.length).trim();
    // create new shape
    const shapeReplacement = createShapeFromLayer(layer, sketch, newName);
    // return new shape
    return shapeReplacement;
};
const createImageLayerImage = (page, layer, sketch) => {
    const layerDuplicate = layer.duplicate();
    // reset asset position on artboard
    layerDuplicate.parent = page;
    // export asset to temp folder
    sketch.export(layerDuplicate, {
        scales: '1, 2',
        formats: 'png',
        // @ts-ignore
        output: NSTemporaryDirectory(),
        ['use-id-for-name']: true,
        ['save-for-web']: true,
        overwriting: true
    });
    // remove asset artboard from page
    layerDuplicate.remove();
    // return AppAsset
    return {
        id: layer.image.id,
        src: {
            // @ts-ignore
            [`1x`]: `${NSTemporaryDirectory()}${layerDuplicate.id}.png`,
            // @ts-ignore
            [`2x`]: `${NSTemporaryDirectory()}${layerDuplicate.id}@2x.png`
        },
    };
};
const createGradientFillImage = (page, layer, sketch) => {
    // get enabled gradients
    const activeGradients = layer.style.fills.filter((fill) => {
        return fill.enabled && fill.fillType === 'Gradient';
    });
    // get top gradient fill
    const topGradient = activeGradients[activeGradients.length - 1];
    // create new layer with gradient
    const gradientImage = new sketch.ShapePath({
        parent: page,
        frame: layer.frame,
        style: {
            fills: [topGradient],
            borders: []
        }
    });
    // export image to temp dir
    sketch.export(gradientImage, {
        scales: '1, 2',
        formats: 'png',
        // @ts-ignore
        output: NSTemporaryDirectory(),
        ['use-id-for-name']: true,
        ['save-for-web']: true,
        overwriting: true
    });
    // remove image from page
    gradientImage.remove();
    // return final image
    return {
        id: layer.id,
        src: {
            // @ts-ignore
            [`1x`]: `${NSTemporaryDirectory()}${gradientImage.id}.png`,
            // @ts-ignore
            [`2x`]: `${NSTemporaryDirectory()}${gradientImage.id}@2x.png`,
        }
    };
};
const createImageFillImage = (page, image, sketch) => {
    // get image size
    const width = image.nsimage.size().width;
    const height = image.nsimage.size().height;
    // create image from fill image
    const fillImage = new sketch.Image({
        image: image,
        parent: page,
        frame: { width, height, x: 0, y: 0 }
    });
    // export image to temp dir
    sketch.export(fillImage, {
        scales: '1, 2',
        formats: 'png',
        // @ts-ignore
        output: NSTemporaryDirectory(),
        ['use-id-for-name']: true,
        ['save-for-web']: true,
        overwriting: true
    });
    // remove image from page
    fillImage.remove();
    // return final image
    return {
        id: image.id,
        src: {
            // @ts-ignore
            [`1x`]: `${NSTemporaryDirectory()}${fillImage.id}.png`,
            // @ts-ignore
            [`2x`]: `${NSTemporaryDirectory()}${fillImage.id}@2x.png`
        }
    };
};
const processLayerFills = (page, layer, images, sketch, fillImages = []) => {
    layer.style.fills.forEach((fill) => {
        if (fill.pattern.image !== null && fill.enabled && !images.find(image => image.id === fill.pattern.image.id)) {
            const fillImage = createImageFillImage(page, fill.pattern.image, sketch);
            fillImages.push(fillImage);
        }
        else if (fill.fillType === 'Gradient' && fill.enabled) {
            const gradientImage = createGradientFillImage(page, layer, sketch);
            fillImages.push(gradientImage);
        }
    });
    return fillImages;
};
const isComplexShapePath = (layer) => {
    const hasOpenPath = !layer.closed;
    const notRectangle = layer.shapeType !== 'Rectangle';
    const notOval = layer.shapeType !== 'Oval';
    const isOddShape = notRectangle && notOval;
    const hasDashPattern = layer.style.borderOptions.dashPattern.length > 0;
    return hasOpenPath || isOddShape || hasDashPattern;
};
const processShapePath = (page, layer, images, sketch, callback) => {
    const isComplex = isComplexShapePath(layer);
    const shapePathFillImages = processLayerFills(page, layer, images, sketch);
    if (isComplex) {
        // turn complex shapePaths into shapes
        // makes things easier when divs are styled later
        const shapeReplacement = createShapeFromLayer(layer, sketch);
        const svg = createSvgFromLayer(page, layer, sketch, shapeReplacement.id);
        layer.parent.layers.splice(layer.index, 1, shapeReplacement);
        callback(shapePathFillImages, svg);
    }
    else {
        callback(shapePathFillImages, null);
    }
};
const processShape = (page, layer, images, sketch, callback) => {
    const shapeFillImages = processLayerFills(page, layer, images, sketch);
    const svg = createSvgFromLayer(page, layer, sketch);
    callback(shapeFillImages, svg);
};
const processImage = (page, layer, images, sketch, callback) => {
    if (!images.find(image => image.id === layer.image.id)) {
        const image = createImageLayerImage(page, layer, sketch);
        callback(image);
    }
    else {
        callback(null);
    }
};
const processGroup = (page, layer, sketch, callback) => {
    if (layer.name.startsWith('[srm.svg]')) {
        // create shape to replace group
        const shapeReplacement = createShapeFromGroup(layer, sketch, '[srm.svg]');
        // create svg from group
        const svg = createSvgFromLayer(page, layer, sketch, shapeReplacement.id);
        // splice in shape replacement, splice out old group
        layer.parent.layers.splice(layer.index, 1, shapeReplacement);
        // return callback
        callback(svg);
    }
    else {
        callback(null);
    }
};
const processText = (layer, fonts, callback) => {
    const fontFamily = layer.style.fontFamily;
    //@ts-ignore
    const availableFamilies = NSFontManager.sharedFontManager().availableFontFamilies();
    const availableFamiliesArray = Array.from(availableFamilies, item => String(item));
    const fontAvailable = availableFamiliesArray.includes(fontFamily);
    if (fonts && fontAvailable && !fonts.includes(fontFamily)) {
        callback(fontFamily);
    }
    else {
        callback(null);
    }
};
const processLayer = (page, layer, sketch, images, svgs, fonts, callback) => {
    switch (layer.type) {
        case 'Image':
            processImage(page, layer, images, sketch, (image) => {
                if (image) {
                    images.push(image);
                }
            });
            break;
        case 'Shape':
            processShape(page, layer, images, sketch, (shapeImages, shapeSvg) => {
                images.push(...shapeImages);
                svgs.push(shapeSvg);
            });
            break;
        case 'ShapePath':
            processShapePath(page, layer, images, sketch, (shapePathImages, shapePathSvg) => {
                if (shapePathSvg) {
                    svgs.push(shapePathSvg);
                }
                images.push(...shapePathImages);
            });
            break;
        case 'Text':
            processText(layer, fonts, (font) => {
                if (font) {
                    fonts.push(font);
                }
            });
            break;
        case 'Group':
            processGroup(page, layer, sketch, (groupSvg) => {
                if (groupSvg) {
                    svgs.push(groupSvg);
                }
            });
            break;
    }
    callback({
        images,
        svgs,
        fonts
    });
};
const processLayers = (page, layers, sketch, images = [], svgs = [], fonts = []) => {
    if (layers.length > 0) {
        layers.forEach((layer) => {
            processLayer(page, layer, sketch, images, svgs, fonts, (newAssets) => {
                images = newAssets.images;
                svgs = newAssets.svgs;
                fonts = newAssets.fonts;
            });
            if (layer.type === 'Group') {
                processLayers(page, layer.layers, sketch, images, svgs, fonts);
            }
        });
    }
    return {
        images,
        svgs,
        fonts
    };
};
export const createArtboardImage = (artboard, sketch) => {
    const buffer = sketch.export(artboard, {
        scales: '0.10',
        formats: 'png',
        output: false,
        ['save-for-web']: true
    });
    // create image from buffer data
    const bufferImg = new sketch.Image({
        image: buffer
    });
    const base64 = bufferImg.image.nsdata.base64EncodedStringWithOptions(0);
    return `data:image/png;base64, ${base64}`;
};
export const getAssets = (page, artboard, sketch) => {
    const artboardAssets = processLayers(page, artboard.layers, sketch);
    const artboardImage = createArtboardImage(artboard, sketch);
    return Object.assign(Object.assign({}, artboardAssets), { artboardImage });
};
export default getAssets;
